@import "./common/base";
//目录结构树
.choseQuestion-tree{
  width:258px;
  height:900px;
  float:left;
  background:@choseQustion-bg;
}

//试题显示
.choseQuestion-content{
  height:880px;
  margin-left: 258px;
  padding:10px;

  .question-show{
    height: 880px;
    background:#fff;
    //border: 1px solid #ccc;
    //.border-radius(25px 0px 25px 25px);
    overflow-y:auto ;

  }

  .tab{
    width:100%;
    height: 40px;
    border-bottom:1px solid #ccc;
    background:#e4ecef;
    .choseTab{
      width:260px;
      height:40px;
      background: #fff;
      border:1px solid #ccc;
      border-bottom:1px solid #fff;
      .border-radius(25px 25px 0 0);
    }
    .choseTab-one{
        width:114px;
        height:40px;
        background: #fff;
        border-top:1px solid #ccc;
        border-left:1px solid #ccc;
        .border-radius(25px 0  0);
        border:transparent;
    }
    .choseTab-bor{
      width:30px;
      height:40px;
      border-right:1px solid #ccc;
      border-left:1px solid #ccc;
      border-top:1px solid #ccc ;
      border-left:transparent;
      .border-radius(25px 25px  0 0);

    }
    .choseTab-two{
      width:114px;
      height:40px;
      background:#deecfc;
      border-top:1px solid #ccc;
      border-right:1px solid #ccc;
      .border-radius(0px 25px 0 0);
      border:transparent;
    }
  }
}